.bg-photo{
    position: absolute;
    top: 100px;
    left: 0;
    width: 100%; 
    height: 700px;
    background-image: url(../img/wallpapers.png);
    background-size: cover;
    background-position: center;
    z-index: -1;
}
.btn-ex{
    color: #bf3e2b;
    background-color: #cfe2f3;
    padding: 30px;
    border: 4px solid #f5de6c;
    border-radius: 20px;
    font-weight: bold;
    font-size: 35px;
    width: 300px;
    cursor: pointer;
}
.btn-ex:hover{
    background-color: #f5de6c;
    font-size: 40px;
    color: #bf3e2b;
    border-radius: 20px;
    border: 4px solid #cfe2f3;
    font-weight: bold;
}
.btn-ex-container{
    display: flex;
    justify-content: center;
    margin-top: 500px;
}