.container-log{
    border: 2px solid #ffbf00;
    border-radius: 30px;
    background-color: #5283b0;
    margin: 80px auto;
    width: 40%;
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
h1{
    color: #ffbf00;
    font-weight: bold;
    font-size: 50px;
    margin-bottom: 25px;
}
.sub-cont{
    display: flex;
    flex-direction: row;
    gap: 15px;
}
.inputs{
    display: flex;
    flex-direction: column;
    gap: 25px;
}
.inputs input{
    width: 300px;
    height: 30px;
    border-radius: 20px;
    outline-color: #f5de6c;
}
.titles p{
    font-size: 35px;
    color: #ed8a46;
    font-weight: bold;
}
.titles{
    display: flex;
    flex-direction: column;
    gap: 5px;
}
.btn2{
    background-color: #cfe2f3;
    font-size: 20px;
    color: #bf3e2b;
    padding: 15px 40px;
    border-radius: 20px;
    margin-top: 15px;
    border: 4px solid #f5de6c;
    font-weight: bold;
    cursor: pointer;
}
.btn2:hover{
    background-color: #f5de6c;
    font-size: 25px;
    color: #bf3e2b;
    padding: 15px 40px;
    border-radius: 20px;
    margin-top: 15px;
    border: 4px solid #cfe2f3;
    font-weight: bold;
}