body{
    background-color: #cfe2f3 ;
    font-family: Arial, Helvetica, sans-serif;
}
.container{
    margin-top: 50px;
    margin-bottom: 50px;
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    flex-wrap: wrap;
    
}
.card{
    width: 300px;
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #5283b0;
    gap: 5px;
    border: 2px solid #ffbf00;
    border-radius: 20%;
    padding: 10px;
}
.card  img{
    width: 100px;
    height: 100px;
    border: 2px solid #ffbf00;
    border-radius: 10%;
}
.product-name{
   font-size: 20px;
   font-weight: bold;
}
.product-title{
    font-size: 15px;
    font-weight: bold;
}
.product-price{
    font-size: 20px;
    font-weight: bold;
    color:darkblue;
}
.btn{
    color: #bf3e2b;
    background-color:  azure;
    padding: 10px;
    border: 3px solid #f5de6c;
    border-radius: 20px;
    font-size: 15px;
    margin-right: 5px;
    cursor: pointer;
}
.btn:hover{
    color: #bf3e2b;
    background-color: #f5de6c;
    padding: 10px;
    border: 3px solid azure;
    border-radius: 20px;
    font-weight: bold;
    font-size: 15px;
    margin-right: 5px;
}
.read-a{
    color: #ffbf00;
    cursor: pointer;
}
.read-a:hover{
    color: #bf3e2b;
    text-decoration: underline;
    font-weight: bold;
}
.singleproduct-container{
    width: 80%;
    height: 1000px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #5283b0;
    gap: 20px;
    border: 2px solid #ffbf00;
    border-radius: 20%;
    padding: 10px;
    margin: 30px auto;
}
.singleproduct-container img{
    width: 300px;
    height: 300px;
    border: 2px solid #ffbf00;
    border-radius: 10%;
}
.sp-t{
    color: #ffbf00;
    font-size: 45px;
}
.sp-p{
    color: #bf3e2b;
    font-size: 35px;
}
.sp-d{
    font-size: 30px;
    color: #ffbf00;
}
.btn-sp{
    color: #bf3e2b;
    background-color:  azure;
    padding: 10px;
    border: 3px solid #f5de6c;
    border-radius: 20px;
    font-size: 30px;
    margin-right: 5px;
    cursor: pointer;
}
.btn-sp:hover{
    color: #bf3e2b;
    background-color: #f5de6c;
    padding: 10px;
    border: 3px solid azure;
    border-radius: 20px;
    font-weight: bold;
    font-size: 35px;
    margin-right: 5px;
}