header{
    width: 100%;
    height: 100px;
    background-color:#5283b0 ;
    display: flex;
    justify-content: center;
    align-items: center;
}
header nav{
    width: 90%;
    height: 100%;
    background-color: #5283b0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
}
header nav ul{
    display: flex;
    gap: 15px;
    font-size: 35px;
}
header nav span{
    color: #ffbf00;
    font-weight: bold;
    font-size: 50px;
}
header nav ul a{
    color: #cfe2f3;
    padding: 10px 20px;
}
header nav ul a:hover{
    background-color: #ffffff;
    color: #ffbf00;
    font-weight: bold;
    border-bottom: 4px solid #ffbf00;
}

